@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  height: 100vh;
  background: #dadbd3;
  display: grid;
  place-items: center;
}

.app__body {
  background: #ededed;
  display: flex;
  width: 90vw;
  height: 90vh;
  box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.7);
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.3 1;
}

/* SIDEBAR HEADER - TOP LEFT */

.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.sidebar__header__right {
  display: flex;
  align-items: center;
  min-width: 10vw;
  justify-content: space-between;
}

.sidebar__header__right > .MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}

/* SIDEBAR SEARCH */

.sidebar__search {
  display: flex;
  align-items: center;
  background: #f6f6f6;
  padding: 1rem;
}

.sidebar__search__container {
  display: flex;
  align-items: center;
  background: white;
  height: 29px;
  padding: 10px;
  width: 100%;
  border-radius: 1.5rem;
}

.sidebar__search__container > input {
  border: none;
  margin-left: 10px;
  width: 100%;
  padding: 5px;
  font-family: inherit;
}
.sidebar__search__container > input:focus {
  outline: none;
}

.sidebar__search__container > .MuiSvgIcon-root {
  color: gray;
}

/* SIDEBAR CHAT */

.sidebar__chats {
  background: white;
  flex: 1 1;
}

.sidebarChat {
  display: flex;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
}

.sidebarChat:hover {
  background: #ebebeb;
}

.sidebarChat__info {
  margin-left: 1rem;
}

.sidebarChat__info > h2 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.chat__message {
  position: relative;
  font-size: 1rem;
  padding: 10px;
  border-radius: 1.5rem;
  background: #fff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 25px 30px;
}

.chat__name {
  position: absolute;
  top: -15px;
  font-weight: 800;
  font-size: xx-small;
}

.chat__timestamp {
  margin-left: 10px;
  font-size: x-small;
}

.chat__receiver {
  background: #dcf8c6;
  margin-left: auto;
}

.chat {
  display: flex;
  flex-direction: column;
  flex: 0.7 1;
}

.chat__header {
  padding: 20px;
  display: flex;
  flex: 0.1 1;
  align-items: center;
  border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
  flex: 1 1;
  padding-left: 20px;
}

.chat__headerInfo > h3 {
  margin-bottom: 3px;
  font-weight: 500;
}

.chat__headerInfo > p {
  color: grey;
}

.chat__headerRight {
  display: flex;
  justify-content: space-between;
  min-width: 100px;
}

.chat__body {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
  background-position: center;
  background-repeat: repeat;
}

.chat__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid lightgray;
  height: 62px;
}

.chat__footer > .MuiSvgIcon-root {
  padding: 10px;
  color: grey;
}

.chat__footer > form {
  flex: 1 1;
  display: flex;
}

.chat__footer > form > input {
  flex: 1 1;
  border-radius: 2rem;
  padding: 10px;
  border: none;
  margin: 0 5px;
  font-family: inherit;
}

.chat__footer > form > input:focus {
  outline: none;
}

.chat__footer > form > button {
  display: none;
}

